<template>
  <vs-row>
    <vs-col class="blockInput" w="12">
      <vs-input block v-model="yourName" placeholder="Your name">
        <template #icon>
          <i class='bx bx-user'></i>
        </template>
      </vs-input>
    </vs-col>

    <vs-col class="blockInput" w="12">
      <vs-input block v-model="userName" placeholder="User name">
        <template #icon>
          <i class='bx bxs-user-badge'></i>
        </template>
      </vs-input>
    </vs-col>

    <vs-col class="blockInput" w="12">
      <vs-input
          block
          type="password"
          v-model="password"
          placeholder="Password"
          :progress="getProgress"
          :visiblePassword="hasVisiblePassword"

          @click-icon="hasVisiblePassword = !hasVisiblePassword">
        <template #icon>
          <i v-if="!hasVisiblePassword" class='bx bx-show-alt'></i>
          <i v-else class='bx bx-hide'></i>
        </template>

        <template v-if="getProgress >= 100" #message-success>
          <i class='bx bx-lock-alt' ></i>
          Secure password
        </template>
        <template v-if="getProgress < 100 && password" #message-danger>
          <i class='bx bx-lock-alt' ></i>
          Weak password
        </template>

      </vs-input>
    </vs-col>

    <vs-col class="blockInput" w="12">
      <vs-input block type="password" v-model="confirmPassword" placeholder="Confirm password">
        <template v-if="confirmPasswordStatus && password" #message-success>
          <i class='bx bx-lock-open-alt'></i>
          Passwords match.
        </template>

        <template #icon>
          <i class='bx bx-rotate-left'></i>
        </template>

        <template v-if="!confirmPasswordStatus && password" #message-danger>
          <i class='bx bx-lock-alt' ></i>
          Passwords do not match
        </template>
      </vs-input>
    </vs-col>

    <vs-col class="blockInput" w="12">
      <vs-button @click="registerQuery" :disabled="getProgress < 50 || !confirmPasswordStatus" block>
        Register
      </vs-button>
    </vs-col>
  </vs-row>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    yourName: '',
    userName: '',
    password: '',
    confirmPassword: '',
    hasVisiblePassword: false,
  }),
  methods:{
    registerQuery(){
      axios.post('/reg_t', {
        name: this.yourName,
        user_name: this.userName,
        password: this.password,
        repeed_password: this.confirmPassword
      }).then((response) => {
        console.log(response);
      }).catch(error => console.log(error));
    }
  },
  computed: {
    getProgress() {
      let progress = 0
      if (/\d/.test(this.password)) {
        progress += 20
      }
      if (/(.*[A-Z].*)/.test(this.password)) {
        progress += 20
      }
      if (/(.*[a-z].*)/.test(this.password)) {
        progress += 20
      }
      if (this.password.length >= 6) {
        progress += 20
      }
      if (/[^A-Za-z0-9]/.test(this.password)) {
        progress += 20
      }
      return progress
    },
    confirmPasswordStatus() {
      if(this.password === this.confirmPassword){
        return true;
      }else{
        return false;
      }
    }
  }
}
</script>

<style scoped>
.blockInput {
  margin: 5px;
}
</style>