<template>
  <div>
    <vs-navbar center-collapsed v-model="active">
      <template #left>
        <!--        <img src="../assets/logo.png" alt="">-->
        <span>NotBeSilent</span>
      </template>
      <vs-navbar-item to="/" :active="active == 'guide'" id="guide">
        Home
      </vs-navbar-item>
      <vs-navbar-item to="/about" :active="active == 'docs'" id="docs">
        About
      </vs-navbar-item>
      <vs-navbar-item :active="active == 'components'" id="components">
        Components
      </vs-navbar-item>
      <vs-navbar-item :active="active == 'license'" id="license">
        license
      </vs-navbar-item>
      <template #right>
        <vs-button @click="showModal=!showModal; typeDataModal='auth'" flat>Login</vs-button>
        <vs-button @click="showModal=!showModal; typeDataModal='register'">Get Started</vs-button>
      </template>
    </vs-navbar>

    <vs-dialog v-model="showModal">
      <template #header>
        <h4 class="not-margin">
          Welcome to <b>NotBeSilent</b>
        </h4>
      </template>
      <Authorization v-if="typeDataModal === 'auth'"/>
      <Registration v-if="typeDataModal === 'register'" />
    </vs-dialog>

  </div>
</template>
<script>
import Authorization from "@/components/Auth/Authorization";
import Registration from "@/components/Auth/Registration";
export default {
  components: {Registration, Authorization},
  data: () => ({
    active: 'guide',
    showModal: false,
    typeDataModal: 'auth'
  })
}
</script>

<style scoped>
.not-margin{
  margin: 0;
  font-weight: normal;
  padding: 10px;
}
</style>