<template>
  <vs-row>
    <vs-col class="blockInput" w="12">
      <vs-input block v-model="userName" placeholder="User name">
        <template #icon>
          <i class='bx bx-user'></i>
        </template>
      </vs-input>
    </vs-col>

    <vs-col class="blockInput" w="12">
      <vs-input
          block
          type="password"
          v-model="password"
          placeholder="Password">
        <template #icon>
          <i class='bx bx-lock-alt'></i>
        </template>
      </vs-input>
    </vs-col>

    <vs-col class="blockInput" w="12">
      <vs-button block @click="authQuery">
        Login
      </vs-button>
    </vs-col>
  </vs-row>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    userName: '',
    password: '',
  }),
  methods: {
    authQuery() {
      axios.post('/log_t', {
        user_name: this.userName,
        password: this.password,
      },{
        withCredentials: true
      }).then((response) => {
        console.log('AUTH', response);
        console.log(document.cookie)
        console.log('sessin', sessionStorage)

        // axios.post('/user', {},{
        //   withCredentials: true
        // }).then((response) => {
        //   console.log('USER', response)
        // }).catch((error) => {
        //   console.log(error)
        // })

      }).catch(error => console.log(error));
    }
  },
  computed: {}
}
</script>

<style scoped>
.blockInput {
  margin: 5px;
}
</style>